.btn,.btn_white{background-color:var(--color-accent);color:var(--text-color-white);padding:13px 25px;box-shadow:0px 4px 10px rgba(252,132,45,.5);border-radius:30px;border:none;text-align:center;width:200px;cursor:pointer;font-weight:700;transition:all .5s;position:relative;transition:.5s}.btn:after,.btn_white:after{content:"»";position:absolute;opacity:0;top:13px;right:1px;transition:.5s}.btn:focus,.btn_white:focus,.btn:hover,.btn_white:hover{padding-right:24px;padding-left:8px}.btn:focus:after,.btn_white:focus:after,.btn:hover:after,.btn_white:hover:after{opacity:1;right:10px}.btn_white{background-color:var(--text-color-white);color:var(--color-accent);border:2px solid var(--color-accent);box-shadow:none;padding:11px 25px}.btn_white:after{top:11px}